@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');



.SingleCity{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 2vh;
    width: 100%;
    font-family: 'Lato', sans-serif;
    .House{
        position: fixed;
        font-size: 50px;
        top: 1%;
        left: 1%;
        color: rgba(0, 0, 0, 0.83);
    }

}
.DailyCard{
    margin: 0 auto;
    margin-top: 7vh;
    display: flex;
    height: 20vh;
    justify-content: space-around;
    align-items: center;
    width: 90%;
    //& overflow-x: scroll;   :mobile
}

@media screen and (max-width: 600px) {
    .SingleCity{
    padding-top: 0vh;
    width: 98%;
    margin:  0 auto;
    .House{
        position: absolute;
        font-size: 40px;
        left: 2%;
    }
}
.DailyCard{
    margin: 0 auto;
    margin-top: -8vh;
    height: 40vh;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    overflow-x: scroll;
}
}