.DailyCardWrap{
    width: 13%;
    background-color: rgba(19, 19, 19, 0.945);
    border-radius: 25px;
    .List{
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        height: 200px;
        text-align: center;
        color: aliceblue;
        padding: 15px;
        .Temp{
            font-size: 36px;
            color: rgba(200, 23, 23, 0.688);
        }
        .Icons{
        width: 50%;
        }
    .Dates{
        font-size: 18px;
        }
        .Main{
            font-size: 19px;
        }
    }

}
// !
@media screen and (max-width: 600px) {
    .DailyCardWrap{
    width: 30%;
    border-radius: 15px;
    padding-top: 10px;
    margin: 5px;
    margin-top: 30px;
    .List{
        height: 100px;
        padding: 25px;
        .Temp{
            font-size: 30px;
        }
        .Icons{
        width: 100%;
        }
    .Dates{
        font-size: 10px;
        }
        .Main{
            font-size: 18px;
            padding-bottom: 8px;
        }
    }

}
}
